import detailTop from '@/components/detailComponent/detailTop.vue';
import detailRichText from '@/components/detailComponent/detailRichText.vue';
import detailPopup from '@/components/detailComponent/detailPopup.vue';
import {mapActions,mapState} from 'vuex';

export default {
	components:{
		detailTop,
		detailRichText,
		detailPopup
	},
	data() {
		return {
			isShow:false,
			addCartorBuy:0,     //0代表加入购物车,1代表购买
		}
	},
	onLoad(option){
		const id = option.id;
		
		this.getImages(id);
		this.getDetailInfo(id)
	},
	methods: {
		...mapActions(['getImages','getDetailInfo']),
		clone(){
			this.isShow = false;
		},
		addCart(){
			this.isShow = true;
			this.addCartorBuy = 0;
		
		},
		buy(){
			this.isShow = true;
			this.addCartorBuy = 1;
		}
	},
	computed:{
		...mapState({
			info:state => state.details.info
		}),
		minprice(){
			return (this.info.marketPrice - this.info.price).toFixed(2)
		}
	}
}
